import { Component, Input, OnInit,HostBinding} from '@angular/core';
import { IntegrationConstants } from 'src/app/commonBussinessSRC/psServices/integration/ps-integration-constants';
import { CommonUtils } from '../../psServices/models/common-utils';
import { IOptionsPsButtonApprove, IOptionsPsButtonReject, IOptionsPsContainerPanel, IOptionsPsTemplateApproveRejectData } from '../../psServices/models/ps-common-interface';
import { PsCommonSettings } from '../../psServices/models/ps-common.settings';
import { PsCommonService } from '../../psServices/ps-common/ps-common.service';

import { IOptionsPsComplexAlertController } from '../ps-template-alert-controller/ps-template-alert-controller.template.interfaces';
import { PsTemplateBasePage } from '../ps-template-base/ps-template-base.page';

@Component({
  selector: 'ps-template-approve-reject-data',
  templateUrl: './ps-template-approve-reject-data.page.html',
})
export class PsTemplateApproveRejectData extends PsTemplateBasePage implements OnInit{
  @Input() options: IOptionsPsTemplateApproveRejectData;
  @HostBinding('class') oper_Id;
  enableTranslation: boolean;
  approveOptions: IOptionsPsButtonApprove = {
    labelKey: 'approve_key',
    group: this.options.group,
    postCallFunction: {
      func(returnedData) {
        return new Promise<any>((resolve, reject) => {
          this.executionClass.refreshList(returnedData);
          resolve(true);
        });
      },
      executionClass: this,
      params: []
    }
  };
  rejectOptions: IOptionsPsButtonReject = {
    labelKey: 'reject_key',
    group: this.options.group,
    preCallFunction: {
      func() {
        return new Promise<any>((resolve, reject) => {
          this.executionClass.confirm().then(() => {
            resolve(true);
          }).catch(() => {
            reject()
          })
        });
      },
      executionClass: this,
      params: []
    },
    postCallFunction: {
      func(returnedData) {
        return new Promise<any>((resolve, reject) => {
          this.executionClass.refreshList(returnedData);
          resolve(true);
        });
      },
      executionClass: this,
      params: []
    }
  };
  previewPanelOptions: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: this.common.translate('summary_key'),
    iconName: 'keypad',
    expanded: true
  }
  constructor(private common: PsCommonService) {
    super();
  }

  refreshList(returnedData) {
    //added by ghada, AZDB-5485 for refreshing whole draft list not just JBPM since the template now is being opened from other draft records
    this.common.events.publish('draft:refresh');
  }
  get operId(){
    for (const item of this.options.data){
    if(item.labelkey == "operId") return item.value;
    }
   }
  ngOnInit() {
    this.oper_Id = 'operId_'+this.operId;
   this.enableTranslation =  this.options.isPendingDeletion?true:false;
    this.approveOptions.group = this.options.group;
    this.approveOptions.submitServiceUrl = this.options.isPendingDeletion ? "" :IntegrationConstants.JBPM.serviceUrl.completeBpmTaskFromInterceptor;
    this.rejectOptions.group = this.options.group;
    this.rejectOptions.submitServiceUrl = this.options.isPendingDeletion ? "" :IntegrationConstants.JBPM.serviceUrl.completeBpmTaskFromInterceptor;
    if (this.options.data) {
      this.approveOptions.extraParams = {
        data: this.options.data, ...this.options.extraParams,
        isActionButton:true
      }

      this.rejectOptions.extraParams = {
        data: this.options.data, ...this.options.extraParams,
        isActionButton:true
      }
    }
    this.commonProv.setFormData(this.options.group, this.options.requestObject);
  }


  confirm() {
    let modalID = 'user-confirmation-popup';
    return new Promise<any>((resolve, reject) => {
      const alertController: IOptionsPsComplexAlertController = {
        cssClass: modalID,
        header: 'enter_reason_key',
        inputs: [
          {
            type: 'keyin',
            options: {
              placeHolder: 'reason_key',
              fcName: 'rejectionReason',
              group: this.options.group,
              forceShowOnPreview: true
            }
          }
        ],
        buttons: [
          {
            role: 'cancel',
            options: {
              labelKey: 'cancel_key',
              psClass: 'user-confirmation-popup-cancel-button',
              group: this.options.group
            },
            handler: () => {
              CommonUtils.dismissAllModals(null, null, modalID)
              reject()
            }
          },
          {
            options: {
              labelKey: 'submit_key',
              psClass: 'user-confirmation-popup-submit-button',
              group: this.options.group
            },
            handler: () => {
              this.rejectOptions.extraParams = {
                ...this.rejectOptions.extraParams,
                bpmParametersMap: {
                  reject_reason_out: this.options.requestObject.reason
                }
              }
              CommonUtils.dismissAllModals(null, null, modalID);
              resolve()
            }
          }
        ]
      };
      this.commonProv.presentPsAlert(alertController, modalID);
    });
  }

}
